import React from "react"

export default function Example({ id, title, code, jsx }) {
    return (
        <div className="example" id={id}>
            <div className="title">{title}</div>
            <pre>
                {getCode(code)}
            </pre>
            {jsx}
        </div>
    )

    function getCode(code) {
        if (!code) return null
        return <code>{code}</code>
    }
}