import React, { useState, useRef, useEffect } from 'react';
import DatePicker from "./components/date_picker/date_picker"
// import DatePicker from "react-multi-date-picker"
import Example from "./components/example"
import { ReactComponent as GitHub } from "./assets/github.svg"
import { ReactComponent as NPM } from "./assets/npm.svg"
import screenshot from "./assets/screenshot.jpg"

function App() {
  const [isActive, setIsActive] = useState(false)
  const sidebarRef = useRef(null)

  useEffect(() => {
    document.addEventListener("click", handleClickOutside, false)

    return () => document.removeEventListener("click", handleClickOutside, false)

    function handleClickOutside(event) {
      if (sidebarRef.current && !sidebarRef.current.contains(event.target) && !event.target.className.includes("open-sidebar")) {
        setIsActive(false);
      }
    }
  }, [])

  const logos = {
    npm: <NPM />,
    github: <GitHub />
  }

  const Doc = {
    title: "DatePicker",
    screenshot,
    navbar: {
      title: "React multi Date Picker",
      logos: [
        { name: "github", url: "https://github.com/shahabyazdi/react-multi-date-picker" },
        { name: "npm", url: "https://www.npmjs.com/package/react-multi-date-picker" }
      ]
    },
    examples: [{
      title: "Installation",
      code: "npm install --save react-multi-date-picker"
    }, {
      title: "Usage",
      code: `import react from "react";
  import DatePicker from "react-multi-date-picker";

  export default function Example() {
    return <DatePicker />
  }`
    }, {
      title: "Example",
      examples: [{
        title: "01- Date #1",
        code: `<DatePicker
    date={new Date()}
  />`,
        jsx: <DatePicker
          date={new Date()}
        />
      }, {
        title: "02- Date #2",
        code: `<DatePicker
    date={new Date(2020, 9, 27)}
  />`,
        jsx: <DatePicker
          date={new Date(2020, 9, 27)}
        />
      }, {
        title: "03- String Date #1",
        code: `<DatePicker
      date="2020/08/27"
  />`,
        jsx: <DatePicker
          date="2020/08/27"
        />
      }, {
        title: "04- String Date #2",
        code: `<DatePicker
    date="August 27 2020"
    format="MMMM DD YYYY"
  />`,
        jsx: <DatePicker
          date="August 27 2020"
          format="MMMM DD YYYY"
        />
      }, {
        title: "05- timePicker #1",
        code: `<DatePicker
    date={new Date()}
    format="YYYY-MM-DD HH:mm:ss"
    timePicker
  />`,
        jsx: <DatePicker
          date={new Date()}
          format="YYYY-MM-DD HH:mm:ss"
          timePicker
        />
      }, {
        title: "06- timePicker #2",
        code: `<DatePicker
    date="08/27/2020 12:30:31"
    format="MM/DD/YYYY HH:mm:ss"
    timePicker
  />`,
        jsx: <DatePicker
          date="08/27/2020 12:30:31"
          format="MM/DD/YYYY HH:mm:ss"
          timePicker
        />
      }, {
        title: "07- onlyTimePicker #1",
        code: `<DatePicker
    date={new Date()}
    format="hh-mm-ss A"
    onlyTimePicker
  />`,
        jsx: <DatePicker
          date={new Date()}
          format="hh-mm-ss A"
          onlyTimePicker
        />
      }, {
        title: "08- onlyTimePicker #2",
        code: `<DatePicker
    date="12:30:31 am"
    format="hh:mm:ss a"
    onlyTimePicker
  />`,
        jsx: <DatePicker
          date="12:30:31 am"
          format="hh:mm:ss a"
          onlyTimePicker
        />
      }, {
        title: "09- gregorian",
        code: `<DatePicker
    date="1399/06/05 12:30:24 pm"
    format="YYYY/MM/DD hh:mm:ss a"
    calendar="gregorian"
    local="en"
    timePicker
  />`,
        jsx: <DatePicker
          date="2020/09/01 12:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
        />
      }, {
        title: "10- persian",
        code: `<DatePicker
    date={new Date()}
    format="HH:mm:ss YYYY/MM/DD"
    calendar="persian"
    local="fa"
    timePicker
  />`,
        jsx: <DatePicker
          date={new Date()}
          format="HH:mm:ss YYYY/MM/DD"
          calendar="persian"
          local="fa"
          timePicker
        />
      }, {
        title: "10- arabic",
        code: `<DatePicker
    date={new Date()}
    format="HH:mm:ss YYYY/MM/DD"
    calendar="arabic"
    local="ar"
    timePicker
  />`,
        jsx: <DatePicker
          date={new Date()}
          format="HH:mm:ss YYYY/MM/DD"
          calendar="arabic"
          local="ar"
          timePicker
        />
      }, {
        title: "11- liveChange #1",
        code: `<DatePicker
          date="2020/08/27 12:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
          liveChange
        />`,
        jsx: <DatePicker
          date="2020/08/27 12:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
          liveChange
        />
      }, {
        title: "12- liveChange #2",
        code: `<DatePicker
    date={new Date()}
    format="hh-mm-ss.SSS A"
    liveChange
    onlyTimePicker
  />`,
        jsx: <DatePicker
          date={new Date()}
          format="hh-mm-ss.SSS A"
          liveChange
          onlyTimePicker
        />
      }]
    }, {
      title: "Props",
      code: "",
      jsx: <table>
        <thead>
          <tr>
            <th>Key</th>
            <th>Value</th>
            <th>Default</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>date</td>
            <td>Date,<a target="_blank" rel="noopener noreferrer" href="https://github.com/shahabyazdi/date-object">DateObject</a>, String or Number </td>
            <td>new Date()</td>
          </tr>
          <tr>
            <td>timePicker</td>
            <td>Boolean </td>
            <td>false</td>
          </tr>
          <tr>
            <td>onlyTimePicker</td>
            <td>Boolean </td>
            <td>false</td>
          </tr>
          <tr>
            <td>format</td>
            <td>String </td>
            <td>YYYY/MM/DD</td>
          </tr>
          <tr>
            <td>local</td>
            <td>String (gregorian or persian) </td>
            <td>en</td>
          </tr>
          <tr>
            <td>calendar</td>
            <td>String (en or fa)</td>
            <td>en</td>
          </tr>
          <tr>
            <td>liveChange</td>
            <td>Boolean</td>
            <td>false</td>
          </tr>
          <tr>
            <td>onChange</td>
            <td>Function</td>
            <td>undefined</td>
          </tr>
        </tbody>
      </table>
    }]
  }


  function getExamples(examples, count = 0) {
    if (!examples) return null

    return examples.map((example, index) => {
      return (
        <div key={count + index}>
          <Example
            id={"example" + count + "-" + index}
            title={example.title}
            code={example.code}
            jsx={example.jsx}
          />
          {getExamples(example.examples, count + 1)}
        </div>
      )
    })
  }

  function getSidebarItem(examples, count = 0) {
    if (!examples) return null

    return examples.map((example, index) => {
      return (
        <li key={count + index}>
          <a href={"#example" + count + "-" + index}>{example.title}</a>
          <ul className="sidebar-sub-menu">
            {getSidebarItem(example.examples, count + 1)}
          </ul>
        </li>
      )
    })
  }

  return (
    <div className="App">
      <nav className="navbar">
        <span className="open-sidebar" onClick={toggleSidebar}>&#9776;</span>
        <span className="title">{Doc.navbar.title}</span>
        <div className="nav-images">
          {Doc.navbar.logos.map((logo, index) => <div key={index} className="nav-image">
            <a target="_blank" rel="noopener noreferrer" className={logo.name} href={logo.url}>{logos[logo.name]}</a>
          </div>)}
        </div>
      </nav>
      <ul ref={sidebarRef} className={`sidebar ${isActive ? "active" : ""}`}>
        {getSidebarItem(Doc.examples)}
      </ul>
      <div className="main">
        <div className="screenshot">
          <img src={Doc.screenshot} alt="screenshot" />
        </div>
        {getExamples(Doc.examples)}
      </div>
    </div>
  );

  function toggleSidebar() {
    setIsActive(!isActive)
  }
}

export default App;
