import React from "react"
import Header from "../header/header"
import DayPicker, { isSameDate } from "../day_picker/day_picker.js"
import MonthPicker from "../month_picker/month_picker.js"
import YearPicker from "../year_picker/year_picker.js"
import TimePicker from "../time_picker/time_picker"
import DateObject from "react-date-object"
import "./style.css"

export default class DatePicker extends React.Component {
    constructor(props) {
        super(props)
        this.ref = React.createRef()

        this.state = { local: "en" }
    }

    componentDidMount() {
        let {
            date,
            onlyTimePicker,
            format,
            local = "en",
            calendar = "gregorian",
            liveChange,
            multiple,
            range,
            mustShowDates,
            inputType,
            timePicker
        } = this.props

        let parsedDate, stringDate = ""

        if (multiple && !Array.isArray(date)) {
            if (date) {
                date = [date]
            } else {
                date = []
            }
        }

        if (range && !Array.isArray(date)) date = []
        if (!multiple && !range && Array.isArray(date)) multiple = true
        if (multiple && range) range = false

        if (multiple || range) {
            timePicker = false
            onlyTimePicker = false
        }

        if (Array.isArray(date)) {
            parsedDate = []
            stringDate = []

            for (let $date of date) {
                let [$parsedDate] = this.validate($date, format, calendar, local)

                parsedDate.push($parsedDate)
                stringDate.push($parsedDate.format())
            }

            if (multiple) stringDate = stringDate.join(" , ")
            if (range) stringDate = stringDate.join(" ~ ")

        } else {
            [parsedDate, stringDate] = this.validate(date, format, calendar, local, onlyTimePicker)
        }

        this.setState({
            mustShowYearPicker: false,
            mustShowMonthPicker: false,
            mustShowTimePicker: timePicker || onlyTimePicker ? true : false,
            onlyTimePicker: onlyTimePicker ? true : false,
            isVisible: false,
            format,
            local,
            calendar,
            liveChange,
            multiple,
            range,
            mustShowDates,
            inputType,
            date: getDate(parsedDate),
            selectedDate: parsedDate,
            stringDate,
            year: getYear(parsedDate),
            month: getMonth(parsedDate)
        })

        const handleClickOutside = event => {
            let ref = this.ref

            if (ref.current && !ref.current.contains(event.target) && !event.target.classList.contains("rm-dp-c-p-b-deselect")) {
                this.setState({ ...this.state, isVisible: false })
            }
        }

        document.addEventListener("click", handleClickOutside)

        function getDate(parsedDate) {
            let date = undefined

            if (Array.isArray(parsedDate) && parsedDate[0]) {
                date = parsedDate[0]
            } else if (!Array.isArray(parsedDate) && parsedDate) {
                date = parsedDate
            } else {
                date = new DateObject()
            }

            return date
        }

        function getYear(parsedDate) {
            let year = undefined

            if (Array.isArray(parsedDate) && parsedDate[0]) {
                year = parsedDate[0].year
            } else if (!Array.isArray(parsedDate) && parsedDate) {
                year = parsedDate.year
            } else {
                year = new DateObject().year
            }

            return year
        }

        function getMonth(parsedDate) {
            let month = undefined

            if (Array.isArray(parsedDate) && parsedDate[0]) {
                month = parsedDate[0].month.index
            } else if (!Array.isArray(parsedDate) && parsedDate) {
                month = parsedDate.month.index
            } else {
                month = new DateObject().month.index
            }

            return month
        }
    }

    render() {
        let { name, className, readOnly, style } = this.props

        return (
            <div ref={this.ref} className={`rm-date-picker ${this.state.local === "en" ? "" : "rm-dp-rtl"}`}>
                {this.getInput(name, className, readOnly, style)}
                <div className={`rm-dp-container ${this.state.isVisible ? "active" : ""}`}>
                    <div className="rm-dp-c-wrapper">
                        <Header state={this.state} setState={this.updateState} />
                        <DayPicker state={this.state} setState={this.updateState} />
                        <MonthPicker state={this.state} setState={this.updateState} />
                        <YearPicker state={this.state} setState={this.updateState} />
                        <TimePicker state={this.state} setState={this.updateState} />
                    </div>
                    {this.state.mustShowDates && this.state.multiple ?
                        <div className="rm-dp-c-panel">
                            <div className="rm-dp-c-p-header">Dates</div>
                            <ul className="rm-dp-c-p-body">
                                {Array.isArray(this.state?.selectedDate) ?
                                    this.state?.selectedDate.map(($date, index) => {
                                        return (
                                            <li key={index}>{$date.format()} <button className="rm-dp-c-p-b-deselect" onClick={() => this.deSelect($date)}>+</button></li>
                                        )
                                    })
                                    :
                                    <li>{this.state?.stringDate}</li>
                                }
                            </ul>
                        </div> :
                        null
                    }
                </div>
            </div>
        )
    }

    updateState = state => this.setState(state)

    validate = () => {
        let { date, format, calendar, local, onlyTimePicker } = this.props

        let parsedDate = new DateObject({ date, format, calendar, local })

        if (!parsedDate.isValid) parsedDate = new DateObject({ calendar, local })

        let stringDate = ""

        if (typeof date === "string") stringDate = date
        if (typeof date === "number" || date instanceof Date || date instanceof DateObject) {
            if (date) stringDate = parsedDate.format()
        }

        if (date && typeof date === "string" && onlyTimePicker) {
            if (format) {
                parsedDate.setFormat(format).parse(date)
            } else {
                format = "hh:mm:ss a"
                parsedDate.setFormat(format)
            }
        }

        if (!date && !format && onlyTimePicker) {
            format = "hh:mm:ss a"
            parsedDate.setFormat(format)
            stringDate = ""
        }

        return [parsedDate, stringDate]
    }

    getInput = (name, className, readOnly, style) => {
        let input = undefined
        let { stringDate, range, multiple, inputType } = this.state

        switch (inputType) {
            case "button":
                input = <button
                    type="button"
                    name={name || ""}
                    onClick={this.handleClick}
                    className={className || ""}
                    disabled={readOnly ? true : false}
                    style={style}
                >
                    {getText()}
                </button>

                function getText() {
                    if (!range && !multiple && stringDate) return stringDate
                    return "click to select"
                }

                break
            default:
                input = <input
                    name={name || ""}
                    type="text"
                    value={stringDate || ""}
                    onChange={this.handleChange}
                    onClick={this.handleClick}
                    className={className || ""}
                    readOnly={readOnly ? true : false}
                    style={style}
                    autoComplete="off"
                />
        }

        return input
    }

    handleChange = e => {
        if (this.state.multiple || this.state.range) return

        let value = e.target.value
        let $state = { ...this.state, stringDate: value }

        if (this.state.liveChange) {
            let date = new DateObject($state.selectedDate).parse(value)

            if (date.isValid) $state.selectedDate = date

            $state.date = date
        }

        this.setState($state)
    }

    handleClick = e => {
        let value = e.target.value

        if (!value && !this.state.multiple && !this.state.range) value = this.state.date.format()

        this.setState({
            ...this.state,
            stringDate: value,
            isVisible: true
        })
    }

    deSelect = date => {
        let dates = []
        let formats = []

        for (let $date of this.state.selectedDate) {
            if (!isSameDate($date, date)) {
                dates.push($date)
                formats.push($date.format())
            }
        }

        this.setState({
            ...this.state,
            selectedDate: dates,
            stringDate: formats.join(" , ")
        })
    }
}